<template>
	 <div class="five">
	 	<div class="box_4">
    	<div class="content-box">
    		<h4>提现</h4>
    		<div class="content">
	    		<div class="one">
	    			<img src="../../../assets/u496_03.png"/>
	    		</div>
	    		<div class="two">
	    			<p>您提现的申请已经提交，等待银行处理。</p>
	    			<span>如果银行信息填写错误，导致提现失败，资金将自动退还到您的网银钱包账户。</span>
	    			<p>您可能需要：
	    				<router-link to="/company"><a href="">继续提现</a></router-link>
	    				<a href="">查看提现记录</a>
	    				<a href="">返回我的网银钱包</a>
	    			</p>
	    		</div>   			
    		</div>
    	</div>
	</div>
    </div>
</template>

<script>
import com from '@/dayun/child/tx'
  export default {
    data() {
      return {
        active: 2,
        input: ''
      }
    },
	components: {
		com
	}
  }
</script>

<style lang="less" scoped>
	.five{
		.box_4{
			width: 1600px;
			background: #fafbff;
			float: left;
			margin-left: 2px;
			color: #bbbbbb;
		.content-box{
			margin: 28px;
			width: 1200px;
			border:1px solid #e8e8f4;
			background: #fff;
			padding-bottom: 50px;
			height:921px;
			h4{
				font-weight: bold;
				padding: 16px 0px 12px 20px;
				margin: 0 ;
				border-bottom: 1px solid #d0d0d0;
			}
			.content{
				padding: 100px 200px;
				.one{
					float: left;
					padding: 0 20px;
					img{
						margin-top: 20px;
					}
				}
				.two{
					float: left;
					p:nth-child(1){
						font-size: 20px;
						color: #000;
					}
					span{
						display: block;
						padding: 10px 0;
					}
					p:nth-child(3){
						color: #000;
						a{
							color: #4f9ad3;
							padding: 0 10px;
						}
					}
				}				
			}
		}
		}
	}
</style>


